.login-container {
    background-color: #000000;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  
  .login-card {
    height: 400px;
    width: 30%;
    background-color: #272727;
    border-radius: 12px;
    padding: 50px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
  }
  
  .login-img-1 {
    height: 50px;
    width: 150px;
  }
  
  .login-card-2 {
    margin-top: 25px;
    padding: 20px;
  }
  
  .img-container-login {
    display: flex;
    justify-content: center;
  }
  
  .login-label {
    font-weight: 500;
    color: white;
  }
  
  .input-1-l {
    outline: none;
    width: 90%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #cbd5e1;
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: transparent;
    padding-left: 20px;
    color: white;
  }
  
  .login-butt {
    width: 100%;
    height: 40px;
    border: none;
    border-radius: 8px;
    background-color: #6366f1;
    color: #f8fafc;
    margin-top: 15px;
    cursor: pointer;
  }
  
  @media screen and (max-width: 560px) {
    .login-card {
      width: 100%;
      margin: 30px;
    }
  }
  
  .error-message {
    color: #ff0b37;
  }

  .login-head {
    color: white;
    text-align: center;
    font-size: 30px;
    margin-bottom: 50px;
  }